@use 'base/variables' as v;
@use 'base/mixins' as m;


        

 

.grafico {
    list-style: none;
    display: flex;
    flex-direction: row; /* Mostrar divs de manera horizontal */
    transition: flex 0.5s; /* Transición para una animación suave */


    padding: 0;
    margin-top: 3rem;
    .no-tareas {
        text-align: center;
    }


    .canvas {
        flex: 1; /* Ocupa todo el espacio horizontal disponible */
    transition: flex 0.5s; /* Transición para una animación suave */
    }
    
    .panel {
        flex: 1; /* No ocupa espacio horizontal inicialmente */
       
        transition: flex 0.5s; /* Transición para una animación suave */
    }
    
}

.expanded {
    flex-grow: 1; /* Ocupa todo el espacio disponible */
}

.expand-button {
  
    border: none; /* Esto elimina el borde */
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

/* Estilo para la barra de navegación fija en la parte inferior */
.navbar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #333;
    color: white;
    text-align: center;
    padding: 10px 0;
}

/* Estilo para los botones de filtro */
.filter-button {
    background-color: #555;
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 0 5px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.filter-button:hover {
    background-color: #777;
}