@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-proyectos {

    a {
        color: v.$indigo;
    }
}
.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid(1, 2rem);
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
    @include m.desktop {
        @include m.grid(3, 2rem);
    }

    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: .5rem;

        &:hover {
            background-color: darken(v.$indigo, 5%);
        }

        a {
            color: v.$blanco;
        }
    }
    .eliminar-proyecto {
        background-color: v.$rojo; // Usando una variable para el color rojo, asegúrate de tenerla en tu variables
        border: none;
        color: v.$blanco;
        padding: 0.5rem 1rem;
        border-radius: 0.25rem;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
            background-color: darken(v.$rojo, 10%);
        }
    }
}