/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .listado-tags {
  max-height: 200px;
  /* Ejemplo de altura máxima */
  overflow-y: auto;
  /* Habilita el desplazamiento vertical */
  /* Otros estilos opcionales */
  border: 1px solid #ccc;
  padding: 10px;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}
.formulario .campo .listado-asignaturas {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario .campo .listado-asignaturas > li {
  background-color: #2563EB;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.formulario .campo__asignatura textarea {
  width: 90%;
  /* Opcional: Ajusta el ancho según tus necesidades */
  height: 300px;
  /* Ajusta la altura según tus necesidades */
  resize: vertical;
  /* Permite redimensionar verticalmente */
  overflow-y: auto;
  /* Agrega una barra de desplazamiento vertical cuando sea necesario */
}
.formulario .campo__asignatura {
  display: flex;
  align-items: column;
  margin-bottom: 2rem;
}
.formulario .campo__asignatura label {
  flex: 0 0 10rem;
}
.formulario .campo__asignatura input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}
.formulario .campo__asignatura .listado-asignaturas {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario .campo__asignatura .listado-asignaturas > li {
  background-color: #2563EB;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.formulario .wrapper {
  width: 100%;
  background: #e0dcf7;
  color: black;
}
.formulario .wrapper .section {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  display: none;
}
.formulario .wrapper .section .cursoform {
  align-items: center;
  display: flex;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
.formulario .wrapper .section .cursoform .asignaturas__cursos {
  align-items: center;
  display: flex;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  margin-top: 1rem;
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .formulario .wrapper .section .cursoform .asignaturas__cursos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.formulario .wrapper .section .cursoform .listacurso .listado-tareas {
  list-style: none;
  padding: 0;
  margin-top: 1rem;
  text-align: center;
}
.formulario .wrapper .section .cursoform .listacurso .listado-tareas .curso_lista {
  margin-top: 1rem;
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario .wrapper .section .cursoform .listacurso .listado-tareas .no-tareas {
  text-align: center;
}
@media (min-width: 768px) {
  .formulario .wrapper .section .cursoform {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.formulario .wrapper .section .cursoform .nuevoCurso {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario .wrapper .section .cursoform .tablaCurso {
  display: flex;
  flex-direction: column;
}
.formulario .wrapper .section .cursoButton {
  background-color: #3498db;
  border: none;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
}
.formulario .wrapper .section .cursoButton:hover {
  cursor: pointer;
}
.formulario .wrapper .section .button__wrap {
  background-color: #3498db;
  display: inline-block;
  padding: 8px 30px;
  color: #fff;
  cursor: pointer;
  font-size: 14px !important;
  font-family: "Open Sans", sans-serif !important;
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.formulario .wrapper .is-active, .formulario .wrapper .step-active {
  display: block;
}
.formulario .wrapper .steps {
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: #fff;
  background: #e0dcf7;
  text-align: center;
}
.formulario .wrapper .steps li {
  display: inline-block;
  margin: 20px;
  color: #ccc;
  padding-bottom: 5px;
}
.formulario .wrapper .steps li.is-active {
  border-bottom: 1px solid #3498db;
  color: #3498db;
}
