.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 500ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
}
.modal .formulario dl {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: left;
}
.modal .formulario dt, .modal .formulario input {
  flex-basis: calc(50% - 10px);
  padding: 5px;
  margin: 0;
  box-sizing: border-box;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario input[type=text] {
  border: 1px solid #6b7280;
  border-radius: 0.5rem;
}
.modal .formulario .listado-tags {
  max-height: 200px;
  /* Ejemplo de altura máxima */
  overflow-y: auto;
  /* Habilita el desplazamiento vertical */
  /* Otros estilos opcionales */
  border: 1px solid #ccc;
  padding: 10px;
}
.modal .formulario .listado-tags li {
  /* Estilos anteriores */
  position: relative;
  /* Permite posicionar de forma absoluta el botón */
}
.modal .formulario .listado-tags li button {
  position: absolute;
  /* Posición absoluta para superponer el botón */
  top: 50%;
  /* Alinea verticalmente al centro */
  right: 10px;
  /* Alinea a la derecha con un pequeño espacio */
  transform: translateY(-50%);
  /* Ajusta la posición verticalmente */
  background-color: transparent;
  /* Fondo transparente */
  border: none;
  /* Sin borde */
  color: #777;
  /* Color del texto gris */
  font-size: 12px;
  /* Tamaño de la fuente */
  cursor: pointer;
  /* Cambia el cursor al pasar sobre el botón */
  transition: color 0.3s ease;
  /* Transición suave del color */
}
.modal .formulario .listado-tags li button:hover {
  color: #333;
  /* Cambia el color del texto al pasar el cursor */
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
}
.modal .formulario .submit-nueva-tarea {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .submit-nueva-tarea:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.modal .formulario .cerrar-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

